import { Link as RouterLink } from 'react-router-dom';
import swal from 'sweetalert';
// @mui
import { styled } from '@mui/material/styles';
import { Box, Button, Link, Container, Typography } from '@mui/material';
// layouts
import LogoOnlyLayout from '../../layouts/LogoOnlyLayout';
// routes
import { PATH_AUTH } from '../../routes/paths';
// components
import Page from '../../components/Page';
import Iconify from '../../components/Iconify';
// sections
import { VerifyCodeForm } from '../../sections/auth/verify-code';
import axios from '../../utils/axios';
// hooks
import useAuth from '../../hooks/useAuth';

// ----------------------------------------------------------------------


const RootStyle = styled('div')(({ theme }) => ({
  display: 'flex',
  height: '100%',
  alignItems: 'center',
  padding: theme.spacing(12, 0),
}));

// ----------------------------------------------------------------------
function Resend (email){
  swal({
    title: "Note",
    text: "Do You Want A New OTP?",
    icon: "info",
    button: {
      text: "Proceed",
      closeModal: false,
    },
    dangerMode: true,
  }) .then((willDelete) => {
    if (willDelete) {
  axios.get(`/api/user/resend/${email}/otp`,{
}).then(resp => {
    swal(resp.data.message);
}).catch(error => {
    if(error.message === ''){
      swal('Opps!','An Error Occured','error');
    }else{
    swal("Opps!", error.message, "error");
    }
});
} else {
  swal("Request Cancelled");
}
});
}
export default function VerifyCode() {
  const { user } = useAuth()
  return (
    <Page title="Verify">
      <RootStyle>
        <LogoOnlyLayout />
        <Container>
          <Box sx={{ maxWidth: 480, mx: 'auto' }}>
            <Button
              size="small"
              component={RouterLink}
              to={PATH_AUTH.login}
              startIcon={<Iconify icon={'eva:arrow-ios-back-fill'} width={20} height={20} />}
              sx={{ mb: 3 }}
            >
              Back
            </Button>

            <Typography variant="h3" paragraph>
              Please check your email!
            </Typography>
            <Typography sx={{ color: 'text.secondary' }}>
              We have emailed a 6-digit confirmation code to {user?.email} (spam folder), or  {user?.phone} please enter the code in below box to verify
              your email.
            </Typography>

            <Box sx={{ mt: 5, mb: 3 }}>
              <VerifyCodeForm />
            </Box>

            <Typography variant="body2" align="center">
              Donâ€™t have a code? &nbsp;
              <Link variant="subtitle2" underline="none" onClick={() => {Resend(user?.email)}}>
                Resend code
              </Link>
            </Typography>
          </Box>
        </Container>
      </RootStyle>
    </Page>
  );
}
